      background-color: #3d5380; }
  .btn.loading {
    color: transparent; }

.loading {
  position: relative;
  text-align: center;
  pointer-events: none; }
  .loading:before, .loading:after {
    z-index: 1000;
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    border-radius: 500rem;
    border-style: solid;
    border-width: 0.125rem;
    width: 1.5rem;
    height: 1.5rem;
    margin: -.75rem 0 0 -.75rem; }
  .loading:before {
    border-color: rgba(0, 0, 0, 0.2); }
  .loading:after {
    animation: loading 0.6s linear;
    animation-iteration-count: infinite;
    border-color: #fff transparent transparent; }
  .loading > * {
    visibility: hidden; }

@keyframes loading {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

.loading.large:before, .loading.large:after {
  top: 100px;
  width: 3rem;
  height: 3rem;
  margin: -1.5rem 0 0 -1.5rem; }

